import {createRouter, createWebHashHistory} from 'vue-router'
import DashboardPage from "@/pages/tabs/DashboardPage";
import InboundPage from "@/pages/tabs/InboundPage";
import OutboundPage from "@/pages/tabs/OutboundPage";
import InventoryPage from "@/pages/tabs/InventoryPage";
import FinancePage from "@/pages/tabs/FinancePage";
import GoodsListPage from "@/pages/tabs/GoodsListPage";
import StoreHousePage from "@/pages/tabs/StoreHousePage";
import UserManagerPage from "@/pages/tabs/UserManagerPage";
import UploadCenterPage from "@/pages/tabs/UploadCenterPage";
import EShopPage from "@/pages/tabs/EShopPage";
import BaseSettingPage from "@/pages/tabs/BaseSettingPage";

const routes = [
    {
        path: '/',
        component: () => import('../pages/HomePage'),
        children: [
            {
                path: '',
                name:'0',
                component: DashboardPage,
            },
            {
                path: '/inbound',
                name:'1',
                component: InboundPage,
            },
            {
                path: '/outbound',
                name:'2',
                component: OutboundPage,
            },
            {
                path: '/inventory',
                name:'3',
                component: InventoryPage,
            },
            {
                path: '/finance',
                name:'4',
                component: FinancePage,
            },
            {
                path: '/goods_list',
                name:'5',
                component: GoodsListPage,
            },
            {
                path: '/store_hose',
                name:'6',
                component: StoreHousePage,
            },
            {
                path: '/user_manager',
                name:'7',
                component: UserManagerPage,
            },
            {
                path: '/upload_center',
                name:'8',
                component: UploadCenterPage,
            },
            {
                path: '/e_shop',
                name:'9',
                component: EShopPage,
            },
            {
                path: '/setting',
                name:'10',
                component: BaseSettingPage,
            },
        ],
    },
    {
        path: '/admin',
        component: () => import('../pages/UserPage'),
    },
    {
        path: '/login',
        component: () => import('../pages/LoginAndRegisterPage'),
    }
]

const router = createRouter({
    history: createWebHashHistory(),
    routes, // `routes: routes` 的缩写
})


export default router