import {createStore} from 'vuex'
import Cookies from 'js-cookie'
import {UPDATE_USER} from "@/router/mutations";
import {createWebHashHistory} from "vue-router";

const store = createStore({
    history: createWebHashHistory(),
    state() {
        return {
            token: "rott"
        }
    },
    mutations: {
        [UPDATE_USER](state, token) {
            state.token = token
            console.log("--------------"+token)
            Cookies.set('token', token)
        }
    },
    actions:{

    },
    modules: {
        account: {
            namespaced:true,
            state() {
                return {
                    token: "子模块"
                }
            },
            mutations: {
                ["ABS"](state,token) {
                    console.log(`${state}~~~~~~~~~~~~~~${token}`)
                    console.log(`${this.commit(UPDATE_USER,token)}~~~~~~~~~~~~~~${token}`)
                    console.log(`${this.commit("name/CBS",token)}~~~~~~~~~~~~~~${token}`)

                },
                ["CBS"](state,token){
                    console.log(`${state}~~~~~~~~CBS~~~~~~${token}`)
                }
            },
        },
        name:{
            namespaced:true,
            mutations: {
                ["ABS"](state,token) {
                    console.log(`${state}~~~~~~~~~~~~~~${token}`)
                    console.log(`${this.commit(UPDATE_USER,token)}~~~~~~~~~~~~~~${token}`)
                    console.log(`${this.commit("CBS",token)}~~~~~~~~~~~~~~${token}`)

                },
                ["CBS"](state,token){
                    console.log(`${state}~~~~~~~~name:CBS~~~~~~${token}`)
                }
            },
        }
    }
})
export default store